import {BoxGeometry, MeshBasicMaterial, Scene, PerspectiveCamera, WebGLRenderer,
  Mesh, EdgesHelper, LineBasicMaterial, Object3D, Geometry, Vector3, Line}
  from './threejs/three.module.js';
import OrbitControls from './threejs/OrbitControls.module.js';

/* Constants */
const BOX_W = 0.99;
const SELECT_W = 1.1;
const BOX_COLORS = [
  0xff0000, // 0
  0xffff00, // 1
  0xff00ff, // 2
  0x00ff00, // 3
  0x0000ff, // 4
  0x00ffff, // 5
];
const SELECT_COLOR = 0xffff0;
const BOX_GEO = new BoxGeometry(BOX_W, BOX_W, BOX_W);
const SELECT_GEO = new BoxGeometry(SELECT_W, SELECT_W, SELECT_W);
const BOX_MATERIALS = [/* materials generated in for loop later */];
// Generates MATERIALS for constants object.
for (let i = 0; i < BOX_COLORS.length; ++i) {
  let materials = BOX_MATERIALS;
  let colors = BOX_COLORS;
  materials[i] = new MeshBasicMaterial({
    color: colors[i],
  });
}

/**
 * Create a grid
 * @param  {[type]} widthDim      [description]
 * @param  {[type]} heightDim     [description]
 * @param  {[type]} color         [description]
 * @param  {Number} [opacity=0.2] [description]
 * @return {[type]}               [description]
 */
let createAGrid = function(widthDim, heightDim, color, opacity = 0.2) {
  let material = new LineBasicMaterial({
    color: color,
    opacity: opacity,
  });
  let gridObject = new Object3D();
  let gridGeo = new Geometry();
  let width = widthDim * BOX_W;
  let height = heightDim * BOX_W;
  for (let i = 0; i < widthDim; i++) {
    gridGeo.vertices.push(new Vector3(0, i, 0));
    gridGeo.vertices.push(new Vector3(height, i, 0));
  }
  // for (let i = 0; i <= height; i += steph) {
  //   gridGeo.vertices.push(new Vector3(i, 0, 0));
  //   gridGeo.vertices.push(new Vector3(i, width, 0));
  // }

  // let line = new Line(gridGeo, material);
  // gridObject.add(line);

  return gridObject;
};


/**
 * Creates a new threejs scene to house the game
 * @constructor
 * @param  {Number}  xDim                 Size in the x direction of the board
 * @param  {Number}  yDim                 Size in the y direction of the board
 * @param  {Number}  zDim                 Size in the z direction of the board
 * @param  {Number}  id                    Put the scene within this element.
 * @param  {Boolean} [enAntiAliasing=true] Enable antialiasing in this scene
 */
function PuzzleScene(xDim, yDim, zDim, id, enAntiAliasing=true) {
  let element = document.getElementById(id);
  let elWidth = element.clientWidth;
  let elHeight = element.clientHeight;
  this.scene = new Scene();
  this.camera = new PerspectiveCamera(75, elWidth / elHeight, 0.1, 1000);
  // TODO: figure out the proper camera position based on dimension
  this.camera.position.z = 5;
  this.renderer = new WebGLRenderer({
    antialias: enAntiAliasing,
  });
  this.renderer.setSize(elWidth, elHeight);
  element.appendChild(this.renderer.domElement);

  this.controls = new OrbitControls(
    this.camera, this.renderer.domElement
  );
  this.controls.addEventListener( 'change', this.render );
  this.controls.target.set(0, 0, 0);
  this.controls.enabled = true;
  console.log(this.controls);

  // this.xyGrid = createAGrid(xDim, yDim, yDim, xDim, 0xff0000);
  // this.scene.add(this.xyGrid);

  this.add_box(0, 0, 0, 4);

  this.render();
}

/**
 * Adds a box to the scene and returns the added 3D object.
 * @param  {Number} x           x coordinate of box (integer)
 * @param  {Number} y           y coordinate of box (integer)
 * @param  {Number} z           z coordinate of box (integer)
 * @param  {Number} colorIndex Color of the mesh, determined by the index
 *                              into the BOX_COLORS constant
 * @return {Object}             The mesh added to the scene.
 */
PuzzleScene.prototype.add_box = function(x, y, z, colorIndex) {
  let mesh = new Mesh(BOX_GEO, BOX_MATERIALS[colorIndex]);
  mesh.position.set(x, y, z);
  this.scene.add(mesh);
  return mesh;
};

/**
 * Adds a wirefram to the scene and returns the added 3D object.
 * @param  {[type]}  x              [description]
 * @param  {[type]}  y              [description]
 * @param  {[type]}  z              [description]
 * @param  {Boolean} [select=false] [description]
 * @return {[type]}                 [description]
 */
PuzzleScene.prototype.add_wireframe = function(x, y, z) {
  let mesh = new Mesh(SELECT_GEO);
  let edges = new EdgesHelper(mesh, SELECT_COLOR);
  edges.matrixAutoUpdate = true;
  edges.position.set(x, y, z);
  this.scene.add(edges);
  return edges;
};
PuzzleScene.prototype.render = function() {
  renderer.render(this.scene, this.camera);
};

export default PuzzleScene;



